<template>
  <div class="section big-section" id="about">
    <div class="form-content">
      <div class="form-list">
        <div class="input-item">
          <div class="input-name">
            <i>*</i> 1<span>10-52</span>
          </div>
          <textarea
            class="input-info"
            type="text"
            maxlength="52"
            v-model="options.point"
            v-on:blur="onInputBlur('point')"
          />
        </div>
        <div class="input-item">
          <div class="input-name">
            <i>*</i> 2<span>50-180</span>
          </div>
          <textarea
            maxlength="180"
            class="input-info"
            type="text"
            v-model="options.des"
            v-on:blur="onInputBlur('des')"
          />
        </div>
        <div class="input-item">
          <div class="input-name">
            <i>*</i> 3<span>4:30.3-2M</span>
          </div>
          <div class="upload-file custom-btn btn-4">
            <input
              type="file"
              class="input-file"
              @change="handleSuccess"
              multiple="true"
            />
            <span class="tip"></span>
          </div>
        </div>
        <button class="custom-btn btn-11" @click="saveImgToBoard">
          {{ saveStatus ? "" : "" }}
        </button>
      </div>
      <div class="img-box">
        <img id="aboutImg" class="item-img" src="" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
//
 

export default {
  name: "about",
  data() {
    return {
      saveStatus: false,
      options: {
        point: "",
        des: "",
        img_url: "https://fp.yangcong345.com/middle/1.0.0/asset/blank.png",
      },
    };
  },
  watch: {
    options: {
      deep: true,
      handler() {
        this.drawImg();
        this.saveStatus = false;
      },
    },
  },
  mounted() {
    this.drawImg();
  },
  methods: {
    drawImg() {
      const data = {
        width: 750,
        height: 1150,
        quality: 1,
        ratio: 2,
        output: "png",
        elements: [
          {
            type: "img",
            x: 0,
            y: 0,
            width: 750,
            height: 1150,
            radius: 0,
            content: "https://fp.yangcong345.com/middle/1.0.0/asset/about.png",
          },
          {
            type: "text",
            x: 68,
            y: 148,
            width: 608,
            autoHeight: true,
            lineHeight: 1.6,
            letterSpacing: "1px",
            fontSize: 36,
            bold: 800,
            color: "#333",
            fontFamily: '"buer"',
            content: this.options.point,
          },
          {
            type: "img",
            x: 67.5,
            y: 342,
            width: 615,
            height: 461.25,
            content: this.options.img_url,
          },
          {
            type: "text",
            x: 68,
            y: 830,
            width: 608,
            autoHeight: true,
            lineHeight: 1.6,
            letterSpacing: "1px",
            fontSize: 22,
            color: "#333",
            fontFamily: '"buer-b"',
            content: this.options.des,
          },
        ],
      };
      var img = document.getElementById("aboutImg");
      json2image(
        data,
        (url) => {
          img.src = url;
        },
        (err) => console.error(err)
      );
    },

    onInputBlur(type) {
      switch (type) {
        case "point":
          if (this.options.point.length < 10) {
            this.$toast("10");
          }
          break;
        case "des":
          if (this.options.des.length < 50) {
            this.$toast("50");
          }
          break;
      }
    },
    handleSuccess(e) {
      const file = e.target.files[0];
      // 
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        this.$toast("jpegjpgpng");

        return false;
      }
      // 1M
      if (file.size / (1024 * 1024) > 3) {
        this.$toast("3M");
        return false;
      }
      // base64
      var reader = new FileReader();
      reader.onload = (data) => {
        this.options.img_url = data.target.result; // 
        var tip = document.querySelector(".tip");
        tip.textContent = file.name;
      };
      reader.readAsDataURL(file);
    },

    saveImgToBoard() {
      const options = this.options;
      for (var i in options) {
        if (
          options[i] === "" ||
          options["img_url"] === "https://fp.yangcong345.com/middle/1.0.0/asset/blank.png" ||
          options["des"].length < 50 ||
          options["point"].length < 10
        ) {
          this.$toast("");
          return;
        }
      }
      if (!this.saveStatus) {
        this.$emit("saveImgToDownLoad", {
          id: "aboutImg",
          options: this.options,
        });
        this.$toast("");
        this.saveStatus = true;
      } else {
        this.$toast("");
      }
    },
  },
};
</script>

