  font-size: 14px;
  font-weight: bolder;
  text-align: center;
  overflow: hidden;
  margin: 10px 0;
}

.upload-file span {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #fff;
}

.upload-file:hover {
  font-size: 14px;
  border: none;
}

.upload-file input[type="file"] {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
